import { Block, Action } from '@ali/4ever-cangjie';
import { CodeData } from './types';

export const CODE_CHANGE = Symbol('change-code');
export const CODE_FOCUS_TO_CODE_EDITOR = Symbol('focus-to-code-editor');
export const CODE_FOCUS_TO_TEXT_EDITOR = Symbol('focus-to-text-editor');
export const CODE_REMOVE = Symbol('remove-code-block');
export const CODE_INSERT = Symbol('insert-code-block');
export const CODE_CHANGE_DATA = Symbol('change-code-data');
export const CODE_BLUR = Symbol('blur-code');
export const CODE_TRANSFORM = Symbol('code-transform');

export type ChangeCodePayload = {
  node: Block;
  patches: Array<{
    type: 'insert' | 'delete';
    value: string;
    offset: number;
  }>;
};

export interface RemoveCodePayload {
  node: Block;
}

// TODO：【多鱼】是否还会有 right？ 需要调研 CM 确认，后续希望能优化一下。
export type FocusDirection = 'up' | 'down' | 'left';
export interface FocusToTextEditorPayload {
  direction?: FocusDirection;
  node: Block;
}

export interface FocusToCodeEditorPayload {
  node: Block;
}

export interface InsertCodePayload {
  code?: string;
  theme?: string;
  syntax?: string;
}

export interface ChangeCodeDataPayload {
  data: Partial<CodeData>;
  node: Block;
}

export function changeCode(
  payload: ChangeCodePayload,
): Action<ChangeCodePayload> {
  return {
    type: CODE_CHANGE,
    payload,
  };
}

export function changeCodeData(
  node: Block,
  data: Partial<CodeData>,
): Action<ChangeCodeDataPayload> {
  return {
    type: CODE_CHANGE_DATA,
    payload: { data, node },
  };
}

export function focusToCodeEditor(
  node: Block,
): Action<FocusToCodeEditorPayload> {
  return {
    type: CODE_FOCUS_TO_CODE_EDITOR,
    payload: {
      node,
    },
  };
}

export function focusToTextEditor(
  node: Block,
  direction?: FocusDirection,
): Action<FocusToTextEditorPayload> {
  return {
    type: CODE_FOCUS_TO_TEXT_EDITOR,
    payload: {
      direction,
      node,
    },
  };
}

export function removeCode(node: Block): Action<RemoveCodePayload> {
  return {
    type: CODE_REMOVE,
    payload: {
      node,
    },
  };
}

export function insertCode(
  payload: InsertCodePayload = {},
): Action<InsertCodePayload> {
  return {
    type: CODE_INSERT,
    payload,
  };
}

export function transformCode(
): Action<InsertCodePayload> {
  return {
    type: CODE_TRANSFORM,
    payload: {},
  };
}

export function cancelCode(
): Action<InsertCodePayload> {
  return {
    type: CODE_TRANSFORM,
    payload: {},
  };
}

export function blurCode(): Action<{}> {
  return {
    type: CODE_BLUR,
    payload: {},
  };
}
